import React,{Component} from "react";
import  {SearchBox}  from "./components/search-box/search-box.component.jsx";
import "./App.css"
import { CardList } from "./components/card-list/card-list.components";
class App extends Component{
    constructor(){
        super();

        this.state={
            string:[],
            searchField:""
           
        }
    }
    handleChange = (e) => {
        this.setState({searchField:e.target.value});
    };
    componentDidMount() {
        fetch("https://jsonplaceholder.typicode.com/users").then(response=>response.json()).then(users=>this.setState({string:users}))
    }
    render(){
        const {string,searchField}=this.state;
        const filterSearchField=string.filter(string=>string.name.toLowerCase().includes(searchField.toLowerCase()))
        return(<div className="App">
            <h1>monster rolodex</h1>

    <SearchBox  placeholder="search monster..." handleChange={this.handleChange} />
   
        <CardList   strings={filterSearchField } />
       </div>);
    }
}
export default App;